'use client';

import { ProtectedRoute } from '@/presentation/components/auth/ProtectedRoute';
import { useAuthStore } from '@/presentation/store/useAuthStore';
import { Button } from '@/presentation/components/ui/Button';
import { Card, CardContent, CardHeader, CardTitle } from '@/presentation/components/ui/Card';
import { Logo } from '@/presentation/components/atoms/Logo';
import { useRouter } from 'next/navigation';
import { authRepository } from '@/infrastructure/api/repositories/auth.repository';
import {
  Bell,
  Calendar,
  MapPin,
  MessageSquare,
  Store,
  Users,
  Heart,
  MessageCircle,
  Share2,
  ChevronDown
} from 'lucide-react';
import { useState } from 'react';
import { CulturalCalendar } from '@/presentation/components/features/dashboard/CulturalCalendar';
import { FeaturedBusinesses } from '@/presentation/components/features/dashboard/FeaturedBusinesses';
import { CommunityStats } from '@/presentation/components/features/dashboard/CommunityStats';
import type { CulturalEvent } from '@/presentation/components/features/dashboard/CulturalCalendar';
import type { Business } from '@/presentation/components/features/dashboard/FeaturedBusinesses';
import type { CommunityStatsData } from '@/presentation/components/features/dashboard/CommunityStats';

// Mock data for widgets
const MOCK_CULTURAL_EVENTS: CulturalEvent[] = [
  {
    id: '1',
    name: 'Vesak Day Celebration',
    date: '2025-05-23',
    category: 'religious'
  },
  {
    id: '2',
    name: 'Sri Lankan Independence Day',
    date: '2025-02-04',
    category: 'national'
  },
  {
    id: '3',
    name: 'Sinhala & Tamil New Year',
    date: '2025-04-14',
    category: 'cultural'
  },
  {
    id: '4',
    name: 'Poson Poya Day',
    date: '2025-06-21',
    category: 'holiday'
  }
];

const MOCK_BUSINESSES: Business[] = [
  {
    id: '1',
    name: 'Ceylon Spice Market',
    category: 'Grocery',
    rating: 4.8,
    reviewCount: 125
  },
  {
    id: '2',
    name: 'Lanka Restaurant',
    category: 'Restaurant',
    rating: 4.6,
    reviewCount: 89
  },
  {
    id: '3',
    name: 'Serendib Boutique',
    category: 'Retail',
    rating: 4.9,
    reviewCount: 203
  }
];

const MOCK_COMMUNITY_STATS: CommunityStatsData = {
  activeUsers: 12500,
  activeUsersTrend: { value: '+8.5%', direction: 'up' },
  recentPosts: 450,
  recentPostsTrend: { value: '+12.3%', direction: 'up' },
  upcomingEvents: 2200,
  upcomingEventsTrend: { value: '+5.7%', direction: 'up' }
};

// Mock activity data
interface ActivityItem {
  id: string;
  type: 'event' | 'post' | 'business';
  author: {
    name: string;
    avatar: string;
  };
  content: string;
  location: string;
  timestamp: string;
  likes: number;
  comments: number;
  image?: string;
}

const MOCK_ACTIVITIES: ActivityItem[] = [
  {
    id: '1',
    type: 'event',
    author: { name: 'Priya Perera', avatar: 'PP' },
    content: 'Organizing a traditional Sri Lankan New Year celebration in Toronto! Join us for games, food, and cultural performances.',
    location: 'Toronto, ON',
    timestamp: '2 hours ago',
    likes: 24,
    comments: 8,
    image: undefined
  },
  {
    id: '2',
    type: 'post',
    author: { name: 'Raj Fernando', avatar: 'RF' },
    content: 'Just discovered an amazing Sri Lankan grocery store in Vancouver! They have fresh hoppers and all the spices you need.',
    location: 'Vancouver, BC',
    timestamp: '4 hours ago',
    likes: 15,
    comments: 5
  },
  {
    id: '3',
    type: 'business',
    author: { name: 'Lanka Restaurant', avatar: 'LR' },
    content: 'New menu items available! Try our authentic kottu roti and string hoppers. Special discount for community members this week.',
    location: 'Montreal, QC',
    timestamp: '6 hours ago',
    likes: 42,
    comments: 12
  }
];

export default function DashboardPage() {
  const router = useRouter();
  const { user, clearAuth } = useAuthStore();
  const [selectedLocation, setSelectedLocation] = useState<string>('All Locations');

  const handleLogout = async () => {
    try {
      await authRepository.logout();
    } catch (error) {
      console.error('Logout error:', error);
    } finally {
      clearAuth();
      router.push('/login');
    }
  };

  const getInitials = (name: string) => {
    return name
      .split(' ')
      .map(n => n[0])
      .join('')
      .toUpperCase();
  };

  return (
    <ProtectedRoute>
      <div className="min-h-screen bg-gray-50">
        {/* Sri Lankan Flag Stripe Header */}
        <div className="h-2 bg-gradient-to-r from-saffron via-maroon to-lankaGreen"></div>

        {/* Header */}
        <header className="bg-white border-b sticky top-2 z-10">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-4">
                <Logo size="md" showText={true} />
              </div>

              <div className="flex items-center gap-4">
                {/* Notifications */}
                <button className="relative p-2 hover:bg-saffron-50 rounded-full transition-colors">
                  <Bell className="w-5 h-5 text-gray-600" />
                  <span className="absolute top-1 right-1 w-2 h-2 bg-saffron rounded-full"></span>
                </button>

                {/* User Avatar */}
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 bg-gradient-to-br from-saffron-500 to-maroon-500 rounded-full flex items-center justify-center text-white font-semibold">
                    {getInitials(user?.fullName || 'U')}
                  </div>
                  <div className="hidden md:block">
                    <p className="text-sm font-medium text-gray-900">{user?.fullName}</p>
                    <p className="text-xs text-gray-500">{user?.role}</p>
                  </div>
                </div>

                {/* Logout Button */}
                <Button onClick={handleLogout} variant="outline" size="sm">
                  Logout
                </Button>
              </div>
            </div>
          </div>
        </header>

        <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          {/* Quick Actions */}
          <div className="mb-6">
            <div className="flex flex-col sm:flex-row gap-3">
              <Button
                onClick={() => router.push('/events/create')}
                className="flex-1 sm:flex-none bg-gradient-to-r from-saffron to-maroon hover:from-saffron-600 hover:to-maroon-600"
                variant="default"
              >
                <Calendar className="w-4 h-4 mr-2" />
                Create Event
              </Button>
              <Button
                onClick={() => router.push('/forum/new')}
                className="flex-1 sm:flex-none"
                variant="outline"
              >
                <MessageSquare className="w-4 h-4 mr-2" />
                Post Topic
              </Button>
              <Button
                onClick={() => router.push('/businesses')}
                className="flex-1 sm:flex-none"
                variant="outline"
              >
                <Store className="w-4 h-4 mr-2" />
                Find Business
              </Button>
            </div>
          </div>

          {/* Two Column Layout */}
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Left Column - Activity Feed (2/3 width on large screens) */}
            <div className="lg:col-span-2 space-y-4">
              {/* Location Filter */}
              <Card>
                <CardContent className="p-4">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2 text-sm text-gray-600">
                      <MapPin className="w-4 h-4" />
                      <span>Filter by location:</span>
                    </div>
                    <button className="flex items-center gap-2 px-3 py-1.5 bg-white border border-gray-300 rounded-md hover:bg-gray-50 transition-colors">
                      <span className="text-sm font-medium">{selectedLocation}</span>
                      <ChevronDown className="w-4 h-4 text-gray-500" />
                    </button>
                  </div>
                </CardContent>
              </Card>

              {/* Activity Feed */}
              <div className="space-y-4">
                <h2 className="text-xl font-semibold text-gray-900">Activity Feed</h2>

                {MOCK_ACTIVITIES.map((activity) => (
                  <Card key={activity.id} className="hover:shadow-md transition-shadow">
                    <CardContent className="p-6">
                      {/* Activity Header */}
                      <div className="flex items-start gap-3 mb-4">
                        <div className="w-12 h-12 bg-gradient-to-br from-saffron-500 to-maroon-500 rounded-full flex items-center justify-center text-white font-semibold flex-shrink-0">
                          {activity.author.avatar}
                        </div>
                        <div className="flex-1 min-w-0">
                          <div className="flex items-center justify-between">
                            <div>
                              <p className="font-semibold text-gray-900">{activity.author.name}</p>
                              <div className="flex items-center gap-2 text-sm text-gray-500">
                                <MapPin className="w-3 h-3" />
                                <span>{activity.location}</span>
                                <span></span>
                                <span>{activity.timestamp}</span>
                              </div>
                            </div>
                            <span className={`px-2 py-1 text-xs font-medium rounded-full capitalize ${
                              activity.type === 'event' ? 'bg-saffron-100 text-saffron-700' :
                              activity.type === 'post' ? 'bg-maroon-100 text-maroon-700' :
                              'bg-lankaGreen-100 text-lankaGreen-700'
                            }`}>
                              {activity.type}
                            </span>
                          </div>
                        </div>
                      </div>

                      {/* Activity Content */}
                      <p className="text-gray-700 mb-4 leading-relaxed">{activity.content}</p>

                      {/* Activity Actions */}
                      <div className="flex items-center gap-6 pt-4 border-t">
                        <button className="flex items-center gap-2 text-gray-600 hover:text-saffron transition-colors">
                          <Heart className="w-5 h-5" />
                          <span className="text-sm font-medium">{activity.likes}</span>
                        </button>
                        <button className="flex items-center gap-2 text-gray-600 hover:text-maroon transition-colors">
                          <MessageCircle className="w-5 h-5" />
                          <span className="text-sm font-medium">{activity.comments}</span>
                        </button>
                        <button className="flex items-center gap-2 text-gray-600 hover:text-saffron transition-colors ml-auto">
                          <Share2 className="w-5 h-5" />
                          <span className="text-sm font-medium">Share</span>
                        </button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>

              {/* Load More */}
              <div className="text-center pt-4">
                <Button variant="outline" className="w-full sm:w-auto">
                  Load More Activities
                </Button>
              </div>
            </div>

            {/* Right Column - Widgets (1/3 width on large screens) */}
            <div className="space-y-6">
              {/* Cultural Calendar Widget */}
              <CulturalCalendar events={MOCK_CULTURAL_EVENTS} />

              {/* Featured Businesses Widget */}
              <FeaturedBusinesses businesses={MOCK_BUSINESSES} />

              {/* Community Stats Widget */}
              <CommunityStats stats={MOCK_COMMUNITY_STATS} />

              {/* Community Highlights */}
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg">Community Highlights</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 bg-saffron-100 rounded-full flex items-center justify-center">
                        <Users className="w-5 h-5 text-saffron-600" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">1,234</p>
                        <p className="text-xs text-gray-500">Active Members</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 bg-maroon-100 rounded-full flex items-center justify-center">
                        <Calendar className="w-5 h-5 text-maroon-600" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">42</p>
                        <p className="text-xs text-gray-500">Upcoming Events</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 bg-lankaGreen-100 rounded-full flex items-center justify-center">
                        <Store className="w-5 h-5 text-lankaGreen-600" />
                      </div>
                      <div>
                        <p className="text-sm font-medium text-gray-900">156</p>
                        <p className="text-xs text-gray-500">Local Businesses</p>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        </main>
      </div>
    </ProtectedRoute>
  );
}
